import React from 'react'
import styledComponents from 'styled-components'
import { Fade } from "react-awesome-reveal";




function Section(props) {
  return (
    <Wrap className='sub'  backgorundImage={props.bgImage} >

      <Fade  delay={600} duration={700} >
        <ItemText>
          <h1>{props.title}</h1>
          <p>{props.desc}</p>
        </ItemText>
      </Fade>
      <Fade direction='up' duration={300}>
        <Buttons>
          <ButtonContainer>
            <LeftButton>
              {props.leftBtnTxt}s
            </LeftButton>
            {props.rightBtnTxt &&
              <RightButton>
                {props.rightBtnTxt}
              </RightButton>
            }

          </ButtonContainer>
          <DownArrow src='/images/down-arrow.svg' />
        </Buttons>
      </Fade>
    </Wrap>
  )
}

export default Section


const Wrap = styledComponents.div`
    width:100vw;
    height:100vh;
    background-image: url('/images/');
    background-position:center;
    background-repeat:no-repeat;
    background-size:cover; 
    display:flex;
    justify-content:space-between;
    flex-direction:column;
    // overflow-x:hidden;
    scroll-snap-align:start;
    
    background-image: ${props => `url("/images/${props.backgorundImage}")`}
  

`
const ItemText = styledComponents.div`
    text-align:center;
    padding-top:15vh;

    p
    {
      margin-top:10px;
    }


`

const ButtonContainer = styledComponents.div`
  
  display:flex;
  justify-content:center;
  align-items:center;
  margin-bottom:30px;
  @media (max-width:768px)
  {
    flex-direction:column;
  }





`

const LeftButton = styledComponents.div`
  background-color:rgba(23, 26, 32, 0.8);
  width:256px;
  height:40px;
  padding:4px 24px;
  display:flex;
  justify-content:center;
  align-items:center;
  border-radius:100px;
  font-size:12px;
  text-transform:uppercase;
  color:white;
  cursor:pointer;
  font-weight:700;
  margin:8px;
  


`


const RightButton = styledComponents(LeftButton)`
  background-color:whitesmoke;
  color:rgba(23, 26, 32, 0.8);

`

const DownArrow = styledComponents.img`
  margin-bottom: 20px;
  width:40px;
  animation: animateDown infinite 1.5s;
`


const Buttons = styledComponents.div`
  
`
