import React, {useState} from 'react'
import styledComponents from 'styled-components'
import CloseIcon from '@mui/icons-material/Close';


function Header() {

  const [burgerStatus,setBurgerStatus] = useState(false);


  return (
    <Container>
      <img src="/images/logo.svg" alt="tesla-logo"/>
      <Menu>
        <a href="#">Model S</a>   
        <a href="#">Model 3</a>   
        <a href="#">Model X</a>   
        <a href="#">Model Y</a>   

      </Menu>
      <RightMenu>
        <a href="#">Shop</a>
        <a href="#">Account</a>
        <a href="#"  onClick={()=>setBurgerStatus(true)} >Menu </a>
      </RightMenu>

      <BurgerMenu show={burgerStatus} >
        <div>
          <CloseIcon onClick={()=>setBurgerStatus(false)} className='CloseIcon' />
        </div>
          <p>Model S </p>
          <p>Model Y</p>
          <p>Model 3 </p>
          <p>Model X </p>

      </BurgerMenu>

    </Container>
  )
}

export default Header

const Container = styledComponents.div`

  position : fixed;
  min-height:60px ;
  padding:0px 30px;
  display: flex;
  width: 100vw;
  z-index:1;
  align-items:center; 
  a
  {
    font-weight:600;
    text-tranform:uppercase;
    padding : 0px 10px ;
    flex-wrap: no-wrap;
    cursor: pointer;
  }
  top:0;
  right:0;
  left:0;
  justify-content:space-between;

`


const Menu = styledComponents.div`
  display: flex;
  justify-content:center;
  @media (max-width:768px)
  
   { display:none;}

  

`



const RightMenu = styledComponents.div``



const BurgerMenu = styledComponents.div`
  display:flex;
  position:fixed;
  flex-direction:column;
  background:white;
  height:100vh;
  width:300px;
  transform:${props=>props.show ? "translateX(0%)": "translateX(100%)"};
  transition:0.3s ease;
  top:0;
  right:0;
  bottem:0;
  z-index:16;
  p
  {
    height:50px;
    border-bottom:1px solid grey;
    display:flex;
    text-transform:uppercase;
    padding:20px 15px;
  
    align-items:center;
    text-align:left;
    transition:0.4s ease;
    cursor:pointer;
    margin-top:2px;
  

  }
  p:hover
  {
    background:black;
    color:white;
    transform:translateY(-5px);
  }
 div
 {
   text-align:right;
   padding:20px 20px;
 }

 .CloseIcon
 {
   cursor:pointer;
 }
.CloseIcon:hover
 {
   border:2px solid black;
   border-radius:50%;

 }



  `